/**
 * Weekly materials web page template
 */

export function getWeeklyPageTemplate() {
  return `<!DOCTYPE html>
<html lang="en">
<head>
  <meta charset="UTF-8">
  <meta name="viewport" content="width=device-width, initial-scale=1.0">
  <title>Material Quest Mingguan</title>
  <script src="https://unpkg.com/react@18/umd/react.development.js"></script>
  <script src="https://unpkg.com/react-dom@18/umd/react-dom.development.js"></script>
  <script src="https://unpkg.com/@babel/standalone/babel.min.js"></script>
  <style>
    /* Dark Mode Atlassian/Jira Colors */
    :root {
      --jira-dark-bg: #0D1117;
      --jira-dark-surface: #161B22;
      --jira-dark-surface-hover: #21262D;
      --jira-dark-border: #30363D;
      --jira-dark-text-primary: #F0F6FC;
      --jira-dark-text-secondary: #8B949E;
      --jira-dark-text-muted: #6E7681;
      --jira-blue-500: #1F6FEB;
      --jira-blue-600: #1158C7;
      --jira-blue-400: #388BFD;
      --jira-blue-100: #0D419D;
      --jira-green-500: #238636;
      --jira-green-400: #2EA043;
      --jira-orange-500: #FB8500;
      --jira-orange-400: #FFA657;
      --jira-red-500: #DA3633;
      --jira-purple-500: #8957E5;
    }

    * {
      margin: 0;
      padding: 0;
      box-sizing: border-box;
    }

    body {
      font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', sans-serif;
      background-color: var(--jira-dark-bg);
      color: var(--jira-dark-text-primary);
      line-height: 1.5;
      overflow-x: hidden;
    }

    .app-container {
      display: flex;
      height: 100vh;
      padding: 24px;
      gap: 24px;
      transition: all 0.3s ease;
    }

    .app-container.guide-open {
      gap: 24px;
    }

    /* Left Sidebar Card */
    .sidebar {
      width: 320px;
      background: var(--jira-dark-surface);
      border: 1px solid var(--jira-dark-border);
      border-radius: 12px;
      display: flex;
      flex-direction: column;
      overflow: hidden;
      box-shadow: 0 4px 16px rgba(0, 0, 0, 0.2);
      max-height: calc(100vh - 48px);
      flex-shrink: 0;
    }

    .sidebar-header {
      padding: 24px 20px;
      border-bottom: 1px solid var(--jira-dark-border);
      background: var(--jira-dark-surface);
      position: sticky;
      top: 0;
      z-index: 10;
    }

    .sidebar-header h1 {
      font-size: 20px;
      font-weight: 600;
      color: var(--jira-dark-text-primary);
      margin-bottom: 8px;
    }

    .sidebar-header p {
      font-size: 14px;
      color: var(--jira-dark-text-secondary);
      line-height: 1.4;
    }

    .skills-list {
      flex: 1;
      padding: 16px 0;
    }

    .skill-item {
      display: flex;
      align-items: center;
      padding: 12px 20px;
      cursor: pointer;
      transition: all 0.2s ease;
      border-left: 3px solid transparent;
      margin: 2px 0;
    }

    .skill-item:hover {
      background: var(--jira-dark-surface-hover);
    }

    .skill-item.active {
      background: var(--jira-blue-100);
      border-left-color: var(--jira-blue-500);
    }

    .skill-item.active:hover {
      background: var(--jira-blue-100);
    }

    .skill-icon {
      font-size: 20px;
      width: 32px;
      height: 32px;
      display: flex;
      align-items: center;
      justify-content: center;
      border-radius: 6px;
      background: var(--jira-dark-surface-hover);
      margin-right: 12px;
    }

    .skill-item.active .skill-icon {
      background: var(--jira-blue-500);
    }

    .skill-info h3 {
      font-size: 14px;
      font-weight: 500;
      color: var(--jira-dark-text-primary);
      margin-bottom: 2px;
    }

    .skill-info p {
      font-size: 12px;
      color: var(--jira-dark-text-secondary);
    }

    .skill-badge {
      margin-left: auto;
      padding: 2px 6px;
      border-radius: 3px;
      font-size: 10px;
      font-weight: 500;
      text-transform: uppercase;
    }

    .skill-badge.gathering {
      background: var(--jira-green-500);
      color: white;
    }

    .skill-badge.crafting {
      background: var(--jira-orange-500);
      color: white;
    }

    /* Right Panel */
    .detail-panel {
      flex: 1;
      background: transparent;
      position: relative;
      overflow: hidden;
      display: flex;
      justify-content: flex-start;
      align-items: flex-start;
      transition: all 0.3s ease;
    }

    .app-container.guide-open .detail-panel {
      flex: 0 0 600px;
      justify-content: flex-start;
    }

    .detail-content {
      width: 100%;
      max-width: 600px;
      background: var(--jira-dark-surface);
      border-radius: 12px;
      border: 1px solid var(--jira-dark-border);
      transform: translateX(100%);
      transition: transform 0.3s cubic-bezier(0.4, 0, 0.2, 1);
      overflow-y: auto;
      height: calc(100vh - 48px);
      box-shadow: 0 8px 32px rgba(0, 0, 0, 0.3);
    }

    .detail-content.visible {
      transform: translateX(0);
    }

    /* Guide Panel - Beside Detail Panel */
    .guide-panel {
      flex: 0;
      width: 0;
      background: transparent;
      position: relative;
      overflow: hidden;
      display: flex;
      justify-content: flex-start;
      align-items: flex-start;
      transition: all 0.3s ease;
    }

    .app-container.guide-open .guide-panel {
      flex: 0 0 500px;
      width: auto;
    }

    .guide-content-card {
      width: 100%;
      background: var(--jira-dark-surface);
      border-radius: 12px;
      border: 1px solid var(--jira-dark-border);
      transform: translateX(100%);
      transition: transform 0.3s cubic-bezier(0.4, 0, 0.2, 1);
      overflow-y: auto;
      height: calc(100vh - 48px);
      box-shadow: 0 8px 32px rgba(0, 0, 0, 0.3);
      opacity: 0;
    }

    .guide-content-card.visible {
      transform: translateX(0);
      opacity: 1;
    }

    .detail-header {
      padding: 24px 32px;
      border-bottom: 1px solid var(--jira-dark-border);
      background: var(--jira-dark-surface);
      border-radius: 12px 12px 0 0;
      position: sticky;
      top: 0;
      z-index: 10;
    }

    .detail-header-top {
      display: flex;
      align-items: center;
      margin-bottom: 16px;
    }

    .detail-icon {
      font-size: 32px;
      width: 48px;
      height: 48px;
      display: flex;
      align-items: center;
      justify-content: center;
      border-radius: 8px;
      background: var(--jira-blue-500);
      margin-right: 16px;
    }

    .detail-title h2 {
      font-size: 24px;
      font-weight: 600;
      color: var(--jira-dark-text-primary);
      margin-bottom: 4px;
    }

    .detail-title p {
      font-size: 14px;
      color: var(--jira-dark-text-secondary);
    }

    .close-button {
      margin-left: auto;
      width: 32px;
      height: 32px;
      border: none;
      background: var(--jira-dark-surface-hover);
      color: var(--jira-dark-text-secondary);
      border-radius: 6px;
      cursor: pointer;
      display: flex;
      align-items: center;
      justify-content: center;
      transition: all 0.2s ease;
    }

    .close-button:hover {
      background: var(--jira-dark-border);
      color: var(--jira-dark-text-primary);
    }

    .detail-body {
      padding: 32px;
    }

    .materials-section {
      margin-bottom: 32px;
    }

    .section-title {
      font-size: 16px;
      font-weight: 600;
      color: var(--jira-dark-text-primary);
      margin-bottom: 16px;
      display: flex;
      align-items: center;
      justify-content: space-between;
    }

    .guide-button {
      background: var(--jira-blue-500);
      color: white;
      border: none;
      padding: 8px 16px;
      border-radius: 6px;
      font-size: 12px;
      font-weight: 500;
      cursor: pointer;
      transition: all 0.2s ease;
      display: flex;
      align-items: center;
      gap: 6px;
    }

    .guide-button:hover {
      background: var(--jira-blue-600);
      transform: translateY(-1px);
    }

    /* Guide Sidebar */
    .guide-sidebar {
      display: none;
    }

    .guide-header {
      padding: 24px 32px;
      border-bottom: 1px solid var(--jira-dark-border);
      background: var(--jira-dark-surface);
      border-radius: 12px 12px 0 0;
    }

    .guide-header-top {
      display: flex;
      align-items: center;
      margin-bottom: 16px;
    }

    .guide-icon {
      font-size: 32px;
      width: 48px;
      height: 48px;
      display: flex;
      align-items: center;
      justify-content: center;
      border-radius: 8px;
      background: var(--jira-green-500);
      margin-right: 16px;
    }

    .guide-title h3 {
      font-size: 20px;
      font-weight: 600;
      color: var(--jira-dark-text-primary);
      margin-bottom: 4px;
    }

    .guide-title p {
      font-size: 14px;
      color: var(--jira-dark-text-secondary);
    }

    .guide-close {
      margin-left: auto;
      width: 32px;
      height: 32px;
      border: none;
      background: var(--jira-dark-surface-hover);
      color: var(--jira-dark-text-secondary);
      border-radius: 6px;
      cursor: pointer;
      display: flex;
      align-items: center;
      justify-content: center;
      transition: all 0.2s ease;
    }

    .guide-close:hover {
      background: var(--jira-dark-border);
      color: var(--jira-dark-text-primary);
    }

    .guide-body {
      padding: 32px;
    }

    .guide-overview {
      background: var(--jira-dark-bg);
      border: 1px solid var(--jira-dark-border);
      border-radius: 8px;
      padding: 16px;
      margin-bottom: 24px;
    }

    .guide-overview h4 {
      font-size: 14px;
      font-weight: 600;
      color: var(--jira-dark-text-primary);
      margin-bottom: 8px;
    }

    .guide-overview p {
      font-size: 13px;
      color: var(--jira-dark-text-secondary);
      line-height: 1.5;
    }

    .guide-section {
      margin-bottom: 24px;
    }

    .guide-section h4 {
      font-size: 14px;
      font-weight: 600;
      color: var(--jira-dark-text-primary);
      margin-bottom: 12px;
      display: flex;
      align-items: center;
      gap: 8px;
    }

    .guide-steps {
      list-style: none;
      padding: 0;
    }

    .guide-step {
      background: var(--jira-dark-bg);
      border: 1px solid var(--jira-dark-border);
      border-radius: 6px;
      padding: 12px;
      margin-bottom: 8px;
      font-size: 13px;
      color: var(--jira-dark-text-primary);
      line-height: 1.4;
    }

    .guide-tips {
      list-style: none;
      padding: 0;
    }

    .guide-tip {
      background: var(--jira-blue-100);
      border: 1px solid var(--jira-blue-500);
      border-radius: 6px;
      padding: 10px;
      margin-bottom: 6px;
      font-size: 12px;
      color: var(--jira-dark-text-primary);
      line-height: 1.4;
    }

    .materials-grid {
      display: grid;
      gap: 12px;
    }

    .material-card {
      background: var(--jira-dark-bg);
      border: 1px solid var(--jira-dark-border);
      border-radius: 8px;
      padding: 16px;
      transition: all 0.2s ease;
    }

    .material-card:hover {
      border-color: var(--jira-blue-500);
      background: var(--jira-dark-surface-hover);
    }

    .material-header {
      display: flex;
      justify-content: space-between;
      align-items: center;
      margin-bottom: 8px;
    }

    .material-name {
      font-weight: 500;
      color: var(--jira-dark-text-primary);
      font-size: 14px;
    }

    .material-quantity {
      background: var(--jira-blue-500);
      color: white;
      padding: 4px 8px;
      border-radius: 4px;
      font-size: 12px;
      font-weight: 600;
    }

    .material-location {
      font-size: 12px;
      color: var(--jira-dark-text-secondary);
      line-height: 1.4;
    }

    .skill-image {
      width: 100%;
      max-width: 500px;
      height: auto;
      object-fit: contain;
      border-radius: 8px;
      border: 1px solid var(--jira-dark-border);
      background: var(--jira-dark-bg);
    }

    .empty-state {
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;
      height: calc(100vh - 48px);
      color: var(--jira-dark-text-muted);
      text-align: center;
      padding: 32px;
      width: 100%;
      max-width: 600px;
      background: var(--jira-dark-surface);
      border-radius: 12px;
      border: 1px solid var(--jira-dark-border);
      box-shadow: 0 4px 16px rgba(0, 0, 0, 0.2);
    }

    .empty-state-icon {
      font-size: 48px;
      margin-bottom: 16px;
      opacity: 0.5;
    }

    .empty-state h3 {
      font-size: 18px;
      margin-bottom: 8px;
      color: var(--jira-dark-text-secondary);
    }

    .empty-state p {
      font-size: 14px;
      max-width: 300px;
    }

    @media (max-width: 768px) {
      .app-container {
        flex-direction: column;
        padding: 16px;
        gap: 16px;
      }
      
      .sidebar {
        width: 100%;
        max-height: 300px;
      }
      
      .detail-panel {
        padding: 0;
      }
      
      .detail-content {
        max-width: none;
        width: 100%;
      }
      
      .detail-body {
        padding: 24px;
      }
      
      .detail-header {
        padding: 20px 24px;
      }
      
      .empty-state {
        height: 400px;
      }
    }

    @media (max-width: 1024px) {
      .detail-content {
        max-width: 700px;
      }
      
      .app-container {
        padding: 20px;
        gap: 20px;
      }
    }
  </style>
</head>
<body>
  <div id="root"></div>

  <script type="text/babel">
    // Skills data will be injected here
    const skillsData = SKILLS_DATA_PLACEHOLDER;

    const { useState } = React;

    function WeeklyMaterials() {
      const [selectedSkill, setSelectedSkill] = useState(null);
      const [showGuide, setShowGuide] = useState(false);
      const [guideSkill, setGuideSkill] = useState(null);

      const selectSkill = (skillKey) => {
        setSelectedSkill(skillKey);
        // Close guide when switching skills
        setShowGuide(false);
        setGuideSkill(null);
      };

      const closeDetail = () => {
        setSelectedSkill(null);
      };

      const openGuide = (skillKey) => {
        setGuideSkill(skillKey);
        setShowGuide(true);
      };

      const closeGuide = () => {
        setShowGuide(false);
        setGuideSkill(null);
      };

      return (
        <div className={\`app-container \${showGuide ? 'guide-open' : ''}\`}>
          {/* Left Sidebar */}
          <div className="sidebar">
            <div className="sidebar-header">
              <h1>📋 Material Quest Mingguan</h1>
              <p>Pilih life skill untuk melihat material yang dibutuhkan dan lokasinya. Quest memberikan 7200 XP per skill.</p>
            </div>
            
            <div className="skills-list">
              {Object.entries(skillsData).map(([key, skill]) => (
                <div 
                  key={key}
                  className={\`skill-item \${selectedSkill === key ? 'active' : ''}\`}
                  onClick={() => selectSkill(key)}
                >
                  <div className="skill-icon">{skill.icon}</div>
                  <div className="skill-info">
                    <h3>{skill.title}</h3>
                    <p>{skill.description}</p>
                  </div>
                  <div className={\`skill-badge \${skill.type}\`}>
                    {skill.type}
                  </div>
                </div>
              ))}
            </div>
          </div>

          {/* Right Detail Panel */}
          <div className="detail-panel">
            {selectedSkill ? (
              <div className={\`detail-content \${selectedSkill ? 'visible' : ''}\`}>
                <div className="detail-header">
                  <div className="detail-header-top">
                    <div className="detail-icon">
                      {skillsData[selectedSkill].icon}
                    </div>
                    <div className="detail-title">
                      <h2>{skillsData[selectedSkill].title}</h2>
                      <p>{skillsData[selectedSkill].description}</p>
                    </div>
                    <button className="close-button" onClick={closeDetail}>
                      ✕
                    </button>
                  </div>
                </div>

                <div className="detail-body">
                  <div className="materials-section">
                    <h3 className="section-title">
                      📦 Material yang Dibutuhkan
                      <button 
                        className="guide-button"
                        onClick={() => openGuide(selectedSkill)}
                      >
                        📖 Cara Mendapatkan
                      </button>
                    </h3>
                    <div className="materials-grid">
                      {skillsData[selectedSkill].materials.map((material, index) => (
                        <div key={index} className="material-card">
                          <div className="material-header">
                            <div className="material-name">{material.name}</div>
                            <div className="material-quantity">x{material.quantity}</div>
                          </div>
                          <div className="material-location">{material.location}</div>
                        </div>
                      ))}
                    </div>
                  </div>

                  <div className="materials-section">
                    <h3 className="section-title">
                      🖼️ Gambar Referensi
                    </h3>
                    <img 
                      src={skillsData[selectedSkill].image} 
                      alt={\`\${skillsData[selectedSkill].title} materials\`}
                      className="skill-image"
                      loading="lazy"
                    />
                  </div>
                </div>
              </div>
            ) : (
              <div className="empty-state">
                <div className="empty-state-icon">📋</div>
                <h3>Pilih Life Skill</h3>
                <p>Pilih life skill dari sidebar kiri untuk melihat detail material yang dibutuhkan dan lokasinya.</p>
              </div>
            )}
          </div>

          {/* Guide Panel - Beside Detail Panel */}
          <div className="guide-panel">
            {guideSkill && (
              <div className={\`guide-content-card \${showGuide ? 'visible' : ''}\`}>
                <div className="guide-header">
                  <div className="guide-header-top">
                    <div className="guide-icon">
                      📖
                    </div>
                    <div className="guide-title">
                      <h3>Cara Mendapatkan</h3>
                      <p>Panduan mendapatkan {skillsData[guideSkill].title}</p>
                    </div>
                    <button className="guide-close" onClick={closeGuide}>
                      ✕
                    </button>
                  </div>
                </div>

                <div className="guide-body">
                  <div className="guide-overview">
                    <h4>📝 Ringkasan</h4>
                    <p>{skillsData[guideSkill].guide.overview}</p>
                  </div>

                  <div className="guide-section">
                    <h4>🔧 Langkah-langkah Proses</h4>
                    <ul className="guide-steps">
                      {skillsData[guideSkill].guide.steps.map((step, index) => (
                        <li key={index} className="guide-step">{step}</li>
                      ))}
                    </ul>
                  </div>

                  <div className="guide-section">
                    <h4>🛒 Cara Lain</h4>
                    <ul className="guide-tips">
                      {skillsData[guideSkill].guide.tips.map((tip, index) => (
                        <li key={index} className="guide-tip">{tip}</li>
                      ))}
                    </ul>
                  </div>
                </div>
              </div>
            )}
          </div>
        </div>
      );
    }

    ReactDOM.render(<WeeklyMaterials />, document.getElementById('root'));
  </script>
</body>
</html>`;
}